.about-container {
    max-width: 100%;
    margin: 0 auto;
    padding: 40px 20px;
    background-color: #e0f7e0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    min-height: 500px;
  }
  
  .about-container h1 {
    color: #333;
    margin-bottom: 20px;
  }
  
  .about-container p {
    color: #555;
    line-height: 1.6;
  }