.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 1000;
  }
  
  .navbar .title{
    font-size: 20px;
      margin: 1rem;
      text-decoration: none;
      color: #4CAF4F;
  }
  .navbar ul{
    display: flex;
  }
  .navbar ul li{
    list-style: none;
  }
  .navbar ul li a{
    display: block;
    text-decoration: none;
    color: #4CAF4F;
    padding: 0.5rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
    font-size: 16px;
  }
  .nav-items{
    display: flex;
    flex-direction: row;
  }